<template>
  <el-dialog
    title=""
    width="60%"
    :visible.sync="dialogVisible"
    :before-close="closeDialog"
    @open="openDialog">

    <el-form
      :model="form"
      :rules="rules"
      ref="form">
      <!--  -->
      <el-form-item label="ID" prop="id" :label-width="'120px'">
        <el-input v-model="form.id" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="type" :label-width="'120px'">
        <el-input v-model="form.type" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="text.value" :label-width="'120px'">
        <el-input v-model="form.text.value"></el-input>
      </el-form-item>
      <!-- fault-node -->
      <el-form-item
        v-if="form.type === 'fault-node'"
        label=""
        prop="properties.flevel"
        :label-width="'120px'"
        style="text-align: left;">
        <el-select
          v-model="form.properties.flevel"
          placeholder=""
          clearable>
          <el-option
            v-for="level in faultLevelList"
            :key="level.value"
            :label="level.text"
            :value="level.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <!-- switch-node -->
      <el-form-item
        v-if="form.type === 'switch-node'"
        label="/"
        prop="properties.normalState"
        :label-width="'120px'"
        style="text-align: left;">
        <el-switch
          v-model="form.properties.normalState"
          active-color="#13ce66"
          inactive-color="#ff4949">
        </el-switch>
      </el-form-item>
      <!-- algorithm-node -->
      <el-form-item
        v-if="form.type === 'algorithm-node'"
        label=""
        prop="properties.algorithm"
        :label-width="'120px'"
        style="text-align: left;">
        <el-select
          v-model="form.properties.algorithm"
          placeholder=""
          clearable>
          <el-option
            v-for="algorithm in algorithmList"
            :key="algorithm.value"
            :label="algorithm.text"
            :value="algorithm.value"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="closeDialog"> </el-button>
      <el-button type="primary" @click="formDataUpdate"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'EditDialog',
  props: {
    dialogVisible: {
      type: Boolean
    },
    formData: {
      type: Object,
      required: true
    },
  },
  data () {
    return {
      form: {
        text: {
          value: ''
        },
        properties: {
          normalState: true,
          flevel: 0
        }
      },
      // 
      rules: {
        'text.value': [{ required: true, message: '', trigger: 'blur' }],
        'properties.flevel': [{ required: true, message: '', trigger: 'change' }],
        'properties.algorithm': [{ required: true, message: '', trigger: 'change' }],
      },
      faultLevelList: [
        {
          value: 0,
          text: ''
        },
        {
          value: 1,
          text: ''
        },
        {
          value: 2,
          text: ''
        },
        {
          value: 3,
          text: ''
        },
      ],
      algorithmList: [
        {
          value: 0,
          text: ''
        },
        {
          value: 'lof',
          text: ''
        },
        {
          value: 'dsPot',
          text: ''
        },
      ]
    }
  },
  computed: {

  },
  methods: {
    formDataUpdate () {
      this.$refs['form'].validate((valid, errs) => {
        if (valid) {
          this.$emit('data-update', this.form)
          this.closeDialog()
        } else {

        }
      })
    },
    closeDialog () {
      this.$emit('update:dialogVisible', false)
    },
    openDialog () {
      this.form = {...this.formData}
    },
  },
  watch: {

  }
}
</script>


